"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod"
import * as z from "zod"
import { Loader2, Upload, X } from "lucide-react"
import { toast } from "sonner"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form"
import { useSupabase } from "@/src/components/providers/supabase-provider"
import { createApiClient } from "@/src/lib/api/client"

const technicianSchema = z.object({
  name: z.string().min(2, "Name must be at least 2 characters"),
  phone: z.string().optional(),
  notes: z.string().optional(),
})

type TechnicianFormValues = z.infer<typeof technicianSchema>

export function AddTechnicianForm() {
  const router = useRouter()
  const { supabase, session } = useSupabase()
  const [isLoading, setIsLoading] = useState(false)
  const [imageFile, setImageFile] = useState<File | null>(null)
  const [imagePreview, setImagePreview] = useState<string | null>(null)

  const form = useForm<TechnicianFormValues>({
    resolver: zodResolver(technicianSchema),
    defaultValues: {
      name: "",
      phone: "",
      notes: "",
    },
  })

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      if (file.size > 5 * 1024 * 1024) {
        toast.error("Image too large", { description: "Max file size is 5MB" })
        return
      }
      setImageFile(file)
      const reader = new FileReader()
      reader.onloadend = () => {
        setImagePreview(reader.result as string)
      }
      reader.readAsDataURL(file)
    }
  }

  const removeImage = () => {
    setImageFile(null)
    setImagePreview(null)
  }

  const uploadImage = async (file: File, name: string): Promise<string | null> => {
    try {
      // User suggestion: technician name - created at (timestamp)
      // Sanitize name for filename
      const sanitizedName = name.replace(/[^a-z0-9]/gi, '-').toLowerCase()
      const timestamp = Date.now()
      const filePath = `${sanitizedName}-${timestamp}`
      
      const { data, error } = await supabase.storage
        .from("technician-photos") // Bucket name
        .upload(filePath, file)

      if (error) throw error

      const { data: publicUrlData } = supabase.storage
        .from("technician-photos")
        .getPublicUrl(filePath)

      return publicUrlData.publicUrl
    } catch (error: any) {
      console.error("Storage upload error:", error)
      toast.error("Failed to upload image", { description: error.message })
      return null
    }
  }

  async function onSubmit(data: TechnicianFormValues) {
    if (!session) return
    setIsLoading(true)

    try {
      let photoUrl = undefined

      if (imageFile) {
        const url = await uploadImage(imageFile, data.name)
        if (url) photoUrl = url
        else {
           // If upload failed, stop submission? Or continue without photo?
           // Let's stop and let user decide/retry
           setIsLoading(false)
           return
        }
      }

      const api = createApiClient(session)
      await api.technicians.create({
        ...data,
        photo_url: photoUrl,
      })

      toast.success("Technician created successfully")
      router.push("/admin/technicians")
      router.refresh()
    } catch (error: any) {
      console.error(error)
      toast.error("Failed to create technician", { description: error.message })
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="max-w-2xl mx-auto">
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
          
          {/* Image Upload Section */}
          <div className="space-y-4">
            <FormLabel>Profile Photo</FormLabel>
            <div className="flex items-center gap-6">
              <div className="relative h-32 w-32 rounded-full border-2 border-dashed border-gray-300 flex items-center justify-center overflow-hidden bg-gray-50">
                {imagePreview ? (
                  <>
                    <img src={imagePreview} alt="Preview" className="h-full w-full object-cover" />
                    <button
                      type="button"
                      onClick={removeImage}
                      className="absolute inset-0 bg-black/40 flex items-center justify-center opacity-0 hover:opacity-100 transition-opacity"
                    >
                      <X className="h-8 w-8 text-white" />
                    </button>
                  </>
                ) : (
                  <Upload className="h-8 w-8 text-gray-400" />
                )}
              </div>
              <div className="space-y-2">
                <Input
                  type="file"
                  accept="image/*"
                  onChange={handleImageChange}
                  className="max-w-xs"
                />
                <p className="text-xs text-muted-foreground">
                  Recommended: Square image, max 5MB.
                </p>
              </div>
            </div>
          </div>

          <div className="grid gap-6 md:grid-cols-2">
            <FormField
              control={form.control}
              name="name"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Full Name</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g. John Doe" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <FormField
              control={form.control}
              name="phone"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Phone Number</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g. +1 555 000 0000" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          </div>

          <FormField
            control={form.control}
            name="notes"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Notes (Optional)</FormLabel>
                <FormControl>
                  <Textarea
                    placeholder="Certifications, skills, or other details..."
                    className="min-h-[100px]"
                    {...field}
                  />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />

          <div className="flex justify-end gap-4">
            <Button
              type="button"
              variant="outline"
              onClick={() => router.back()}
              disabled={isLoading}
            >
              Cancel
            </Button>
            <Button type="submit" disabled={isLoading} className="bg-[#00C49A] hover:bg-[#00A07D] text-white">
              {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
              Create Technician
            </Button>
          </div>
        </form>
      </Form>
    </div>
  )
}
